function initRating() {
    const ratings = document.querySelectorAll('.rating');

    if (ratings.length != 0) {
        ratings.forEach(item => {
            let ratingValue = item.querySelector('.rating__wrapper').dataset.totalValue;
            let ratingField = item.querySelector('.rating__current');
            
            ratingField.innerText = `${ratingValue}`;
        });
        
    }
}

initRating();