let mySwiper = undefined;

function initSwiper() {
    const orderBox = document.querySelector('.order__box');
    var screenWidth = window.innerWidth;

    if (screenWidth > 1023 && mySwiper == undefined && orderBox) {
        mySwiper = new Swiper(".order__box", {
            slidesPerView: "auto",
            slidesPerGroup: 3,
            loopedSlides: 15,
            loop: true, 
            
            navigation: {
                nextEl: ".order__btn-right",
                prevEl: ".order__btn-left",
            },
        });
    } else if (screenWidth <= 1023 && mySwiper != undefined) {
        mySwiper.destroy();
        mySwiper = undefined;
    }
}

initSwiper();

var optimizedResize = (function () {
    var callbacks = [],
        running = false;

    // fired on resize event
    function resize() {
        if (!running) {
            running = true;

            if (window.requestAnimationFrame) {
                window.requestAnimationFrame(runCallbacks);
            } else {
                setTimeout(runCallbacks, 66);
            }
        }
    }

    // run the actual callbacks
    function runCallbacks() {
        callbacks.forEach(function (callback) {
            callback();
        });

        running = false;
    }

    // adds callback to loop
    function addCallback(callback) {
        if (callback) {
            callbacks.push(callback);
        }
    }

    return {
        // public method to add additional callback
        add: function (callback) {
            if (!callbacks.length) {
                window.addEventListener("resize", resize);
            }
            addCallback(callback);
        },
    };
})();

optimizedResize.add(function () {
    initSwiper();
});



function initOrderBoxPrompt() {
    const Items = document.querySelectorAll('.order__item');

    Items.forEach(element => {
        element.addEventListener('mouseenter', (e) => {
            if (window.innerWidth >= 1024) {
                let id = element.getAttribute('data-id');
                let prompt = document.getElementById(id);
                let box = element.getBoundingClientRect();
                let left = box.left + window.pageXOffset;
                let top = box.top + window.pageYOffset;

                prompt.classList.add('active');
                requestAnimationFrame(() => {
                    prompt.style.transform = `translate3d(calc(${left}px), calc(${top - 60}px), 0) `;
                });

                element.addEventListener('mouseleave', () => {
                    prompt.classList.remove('active');
                });
            }
        });
    });
}

initOrderBoxPrompt();