var $range = $("#range");
var $input = $("#rangeInput");
var instance;
let min = Number($($range).attr("data-min"));
let max = Number($($range).attr("data-max"));



$range.ionRangeSlider({
    skin: "round",
    type: "single",
    onStart: function (data) {
        $input.prop("value", data.from);
    },
    onChange: function (data) {
        $input.prop("value", data.from);
    }
});

instance = $range.data("ionRangeSlider");

$input.on("change", function () {
    let val = $(this).prop("value");

    if (val < min) {
        val = min;
    } else if (val > max) {
        val = max;
    }

    instance.update({
        from: val
    });

    $($input).prop("value", val);
});

$('.order__range-btn--minus').on('click', function () {
    let minus = Number($(this).attr("data-minus"));
    let val = Number($($input).prop("value")) - minus;

    if (val < min) {
        val = min;
    }

    $($input).prop("value", val);
    instance.update({
        from: val
    });
});

$('.order__range-btn--plus').on('click', function () {
    let plus = Number($(this).attr("data-plus"));
    let val = Number($($input).prop("value")) + plus;

    if (val > max) {
        val = max;
    }

    $($input).prop("value", val);
    instance.update({
        from: val
    });
});