function animIntro() {
    const items = document.querySelectorAll('.intro__illustration-item');

    function initAnim() {
        setTimeout(() => {
            items.forEach(item => {
                item.classList.add("step1");
            });
        }, 2000);

        setTimeout(() => {
            items[0].classList.add("step2");
        }, 4000);

        setTimeout(() => {
            items[1].classList.add("step2");
        }, 4500);

        setTimeout(() => {
            items.forEach(item => {
                item.classList.remove("step1", "step2");
            });
        }, 5000);
    }

    if (items.length != 0) {
        initAnim();

        setInterval(() => {
            initAnim();
        }, 30000);
    }
}

animIntro();
